<template>
  <v-list two-line>
    <v-list-tile @click="toggleTarea(tarea)" target="_blank">
      <v-list-tile-content>
        <v-list-tile-title v-text="tarea.nombre">{{tarea.nombre}}</v-list-tile-title>
      </v-list-tile-content>
    </v-list-tile>
  </v-list>
</template>
<script>
import {mapState,mapMutations} from 'vuex'
export default {
  name: "Tarea",
  props:['tarea'],
  data: () => ({}),
  methods: { ...mapMutations(['toggleTarea'])},
}
</script>
<style lang="" scoped>
</style>



